export default {
  data() {
    return {
      year: new Date(),
      area: '',
      title2: '',
      title3: '',
      title4: '',
      column1: ['', '', '', '', ''],
      column2: [
        { text: '', disabled: true },
        { text: '' },
        { text: '' }
      ],
      column3: {
        : ['', '', '', '', ''],
        : ['', '', '', '', '']
      },
      toastContent: (value, index) => `${value}, ${index}`
    }
  },
  computed: {
    columns() {
      const column = this.column3;
      return [
        {
          values: Object.keys(column),
          className: 'column1'
        },
        {
          values: column[Object.keys(column)[0]],
          className: 'column2',
          defaultIndex: 2
        }
      ];
    }
  },
  methods: {
    onChange1(picker, value, index) {
      Toast(this.toastContent, value, index);
    },
    onChange2(picker, values) {
      picker.setColumnValues(1, this.column3[values[0]]);
    },
    onConfirm(value, index) {
      Toast(this.toastContent, value, index);
    },
    onCancel() {
      Toast(this.cancel);
    }
  }
};



